<template>
  <div class="school-year">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="tabs" @tab-click="handleClick">
        <el-tab-pane label="学年管理" name="year"></el-tab-pane>
        <el-tab-pane label="学期管理" name="semester"></el-tab-pane>
      </el-tabs>
      <school-year v-if="tabs === 'year'"></school-year>
      <semester v-if="tabs === 'semester'"></semester>
    </main-part>
  </div>
</template>
<script>
  import schoolYear from './SchoolYear'
  import semester from './Semester'
  export default {
    components:{
      schoolYear,
      semester
    },
    data() {
      return {
        nav: [{name: "学年学期管理", path: ""}],
        tabs: this.$route.query.type || 'year',
      }
    },
    methods: {
      handleClick() {
        this.$router.push({
          path: this.$route.path,
          query:{
            type: this.tabs
          }
        })
      }
    },
    mounted() {

    }
  }
</script>
