<template>
  <div class="school-year">
    <div @click="showYearDialog()" class="btn-main-deepen btn-oper">增加学年</div>
    <search-box
        v-show="table.query.noticeType === 'RECRUIT'"
        :searchData="searchData" :table="table"></search-box>
    <resize-table>
      <table class="table">
        <thead>
          <tr>
            <th width="25%">学年名称</th>
            <th width="25%">学年开始日期</th>
            <th width="25%">学年结束日期</th>
            <th width="25%">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in table.list">
            <td>{{item.label}}</td>
            <td>{{item.startDate | substrTime}}</td>
            <td>{{item.endDate | substrTime}}</td>
            <td>
              <span @click="showYearDialog(item)" class="oper">编辑</span>
            </td>
          </tr></tbody>
      </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
    <el-dialog width="540px" :title="yearTitle" class="dialog" :visible.sync="yearDialog">

      <div class="item">
        <span class="title imp">学年名称</span>
        <el-input class="input" v-model="yearForm.label" placeholder="请输入学年名称"></el-input>
      </div>
      <div class="item">
        <span class="title imp">开始日期</span>
        <el-date-picker
            v-model="yearForm.startDate"
            type="date" class="input"
            placeholder="请选择开始日期"
        ></el-date-picker>
      </div>
      <div class="item">
        <span class="title imp">结束日期</span>
        <el-date-picker
            v-model="yearForm.endDate"
            type="date" class="input"
            placeholder="请选择结束日期"
        ></el-date-picker>
      </div>
      <div class="btns">
        <div @click="editSchoolYear" class="btn-submit btn-year-add mr15">确认</div>
        <div @click="yearDialog = false" class="btn-clear">取消</div>
      </div>

    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        searchData: [
          {type: 'input', placeholder: '学年名称'}
        ],
        table: {
          url: '/school/querySchoolYearPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: ''
          }
        },
        // 学年弹窗
        yearDialog: false,
        yearTitle: '新增学年',
        // 新增编辑学年表单
        yearForm: {
          schoolYearId: null,
          label: '',
          startDate: '',
          endDate: ''
        }
      }
    },
    methods: {
      // 展示学年弹窗
      showYearDialog(item) {
        if (item) {
          this.yearTitle = '编辑学年'
          this.yearForm = {
            schoolYearId: item.id,
            label: item.label,
            startDate: item.startDate.replace(' ', 'T'),
            endDate: item.endDate.replace(' ', 'T')
          }
        } else {
          this.yearTitle = '新增学年'
          this.yearForm = {
            label: '',
            startDate: '',
            endDate: ''
          }
        }
        this.yearDialog = true
      },
      // 新增 编辑 学年
      async editSchoolYear() {
        if (!this.yearForm.label) {
          this.$message.warning('请输入学年名称')
          return
        }
        if (!this.yearForm.startDate) {
          this.$message.warning('请选择开始日期')
          return
        }
        if (!this.yearForm.endDate) {
          this.$message.warning('请选择结束日期')
          return
        }
        let query = this.yearForm
        query.startDate = new Date(query.startDate).$Format('yyyy-MM-dd') + ' 00:00:00'
        query.endDate = new Date(query.endDate).$Format('yyyy-MM-dd') + ' 00:00:00'
        let url = '/school/addSchoolYear.do'
        let text = '新增成功'
        if (this.yearTitle === '编辑学年') {
          url = '/school/updateSchoolYear.do'
          text = '修改成功'
        }
        await this.$ajax({
          url,
          el: '.btn-year-add',
          noText: false,
          query,
        })
        this.$remind(text)
        this.yearDialog = false
        this.$search(this.table)
      },
    },
    mounted() {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .school-year
    .dialog
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .input
        width: calc(100% - 80px)
      .item
        margin-bottom: 20px
      .teacher-list
        font-size: $font-small
        td, th
          height: 34px
          line-height: 34px
      .btns
        text-align: center
    .btn-oper
      position: absolute
      right: 15px
      z-index: 9
      top: 6px
</style>
