<template>
  <div class="school-year">
    <div @click="showYearDialog()" class="btn-main-deepen btn-oper">增加学期</div>
    <search-box
        v-show="table.query.noticeType === 'RECRUIT'"
        :searchData="searchData" :table="table"></search-box>
    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="20%">学年名称</th>
        <th width="20%">学期名称</th>
        <th width="15%">学期开始日期</th>
        <th width="15%">学期结束日期</th>
        <th width="15%">第一周开始日期</th>
        <th width="15%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.yearName}}</td>
        <td>{{item.termName}}</td>
        <td>{{item.startDate | substrTime}}</td>
        <td>{{item.endDate | substrTime}}</td>
        <td>{{item.firstWeekStartDate | substrTime}}</td>
        <td>
          <span @click="showYearDialog(item)" class="oper">编辑</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
    <el-dialog width="540px" :title="yearTitle" class="dialog" :visible.sync="yearDialog">
      <div class="item">
        <span class="title imp">学年</span>
        <el-select v-model="form.schoolYearId" clearable class="input">
          <el-option
              v-for="(item,key) in schoolList"
              :value="item.id" :label="item.label" :key="key"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="title imp">学期名称</span>
        <el-input class="input" v-model="form.name" placeholder="请输入学期名称"></el-input>
      </div>
      <div class="item">
        <span class="title imp">学期起止日期</span>
        <el-date-picker
            class="input"
            @change="changeDate"
            v-model="form.date"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
        </el-date-picker>
      </div>

      <div class="item">
        <span class="title imp">第一周开始日期</span>
        <el-date-picker
            :disabled="!form.startDate"
            v-model="form.firstWeekStartDate"
            type="date" class="input"
            :picker-options="pickerOptions"
            placeholder="请选择第一周开始日期"
        ></el-date-picker>
      </div>
      <div class="btns">
        <div @click="editSchoolYear" class="btn-submit btn-year-add mr15">确认</div>
        <div @click="yearDialog = false" class="btn-clear">取消</div>
      </div>

    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      let vm = this
      return {
        searchData: [
          {type: 'input', placeholder: '学期/学期名称'}
        ],
        table: {
          url: '/school/querySchoolTermPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: ''
          }
        },
        // 学年列表
        schoolList: [],
        // 学期弹窗
        yearDialog: false,
        yearTitle: '新增学期',
        // 新增编辑学期表单
        form: {
          date: [],
          schoolYearId: null,
          name: '',
          firstWeekStartDate: null,
          startDate: null,
          endDate: null
        },
        pickerOptions: {
          disabledDate(time) {
            return  new Date(vm.form.startDate).getTime() > time.getTime()
          },
        },
      }
    },
    methods: {
      // 展示学期弹窗
      showYearDialog(item) {
        this.getYearName()
        if(item){
          this.yearTitle = '编辑学期'
          this.form =  {
            date: [item.startDate.replace(' ', 'T'),item.endDate.replace(' ', 'T'),],
            schoolTermId: item.schoolTermId,
            schoolYearId: item.schoolYearId,
            name: item.termName,
            firstWeekStartDate: item.firstWeekStartDate.replace(' ', 'T'),
            startDate: item.startDate.replace(' ', 'T'),
            endDate: item.endDate.replace(' ', 'T')
          }
        }else{
          this.yearTitle = '新增学期'
          this.form =  {
            date: [],
            schoolYearId: null,
            name: '',
            firstWeekStartDate: null,
            startDate: null,
            endDate: null
          }
        }
        this.yearDialog = true
      },
      // 新增 编辑 学期
      async editSchoolYear(){
        if(!this.form.name){
          this.$message.warning('请输入学期名称')
          return
        }
        if(!this.form.date){
          this.$message.warning('请选择起止时间')
          return
        }
        if(!this.form.firstWeekStartDate){
          this.$message.warning('请选择第一周开始时间')
          return
        }
        if(new Date(this.form.firstWeekStartDate).getTime() > new Date(this.form.endDate).getTime()){
          this.$message.warning('第一周开始时间不能在结束时间之后')
          return
        }
        let query = this.form

        query.startDate = new Date(this.form.date[0]).$Format('yyyy-MM-dd') + ' 00:00:00'
        query.endDate = new Date(this.form.date[1]).$Format('yyyy-MM-dd') + ' 00:00:00'
        query.firstWeekStartDate = new Date(query.firstWeekStartDate).$Format('yyyy-MM-dd') + ' 00:00:00'
        let url = '/school/addSchoolTerm.do'
        let text = '新增成功'
        if(this.yearTitle === '编辑学期'){
          url = '/school/updateSchoolTerm.do'
          text = '修改成功'
        }
        await this.$ajax({
          url,
          el: '.btn-year-add',
          noText: false,
          query,
        })
        this.$remind(text)
        this.$search(this.table)
        this.yearDialog = false

      },
      // 获取所有的学年
      async getYearName(){
        this.schoolList = await this.$ajax({
          url: '/school/querySchoolYearList.do',
        })
      },
      // 日期切换回调
      changeDate(val){
        if(!!val[0]){
          this.form.startDate = val[0].$Format('yyyy-MM-dd hh:mm:ss')
          this.form.endDate = val[1].$Format('yyyy-MM-dd hh:mm:ss')
        }else{
          this.form.endDate = this.form.startDate = null
        }
      }
    },
    mounted() {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .school-year
    .dialog
      .title
        width: 120px
        line-height: 40px
        display: inline-block
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .input
        width: calc(100% - 120px)
      .item
        margin-bottom: 20px
      .teacher-list
        font-size: $font-small
        td, th
          height: 34px
          line-height: 34px
      .btns
        text-align: center
    .btn-oper
      position: absolute
      z-index: 9
      right: 15px
      top: 6px
</style>
